
import VHeader from '@/components/v-header/v-header'
import imageTop from '@/assets/img/default_avatar.png'
import scenicImage from '@/assets/img/scenic.png'
import hotelImage from '@/assets/img/hotel.png'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import Scroll from '@/components/scroll/scroll'
import axios from '@/api/axiosApi'
import { localUser } from '@/assets/js/local'
import { mapGetters, mapMutations } from 'vuex'
import moment from 'moment'
import nativeBridge from '@/assets/js/nativeBridge'

export default {
data () {
return {
imageTop: imageTop,
scenicImage: scenicImage,
hotelImage: hotelImage,
listOrder: ['全部', '待付款', '已付款', '已取消', '已失效'],
listOrders: [],
index: 0,
scrollHeight: ''
}
},
mounted () {

this.scrollHeight = window.innerHeight - 64 - 44
},
created () {
this.pageNum = 1
this.pageSize = 10
this.getWaitPayList(true)
},
computed: {
...mapGetters({
selectTab: 'orderTab'
})
},
watch: {
'$route' (to, from) {
if (to.name === 'Order') {
this.pageNum = 1
this.getWaitPayList(true)
}
}
},
methods: {
...mapMutations({
setOrderTab: 'SET_ORDER_TAB'
}),
toOrderDesc (descId) {
this.$router.push('/user/order/orderDesc/' + descId)
},

getDaysByDateString (dateString1, dateString2) {
return Math.abs(moment(dateString1).diff(moment(dateString2), 'days'))
},

getWaitPayList (isFirst) {
let expire = ''
const userId = localUser.get('id')
if (userId == null) {
return
}
let orderStatusId = ''
if (this.selectTab === 0) {
expire = ''
} else if (this.selectTab === 1) {
expire = false
orderStatusId = 0
} else if (this.selectTab === 2) {
expire = false
orderStatusId = 1
} else if (this.selectTab === 3) {
expire = false
orderStatusId = 2
} else if (this.selectTab === 4) {
expire = true
}
var url = '/v1/orders'
axios.get(this, url, {buyerId: userId, orderStatus: orderStatusId, pageNum: this.pageNum, pageSize: this.pageSize, expire: expire}, (data) => {
for (var i = 0; i < data.length; i++) {
if (data[i].type === 0) {
data[i].liveNum = this.getDaysByDateString(data[i].effectiveDate.split('~')[0], data[i].effectiveDate.split('~')[1])
}
switch (data[i].orderStatus) {
case 0: data[i].orderStatusDesc = '待付款'
break
case 1: data[i].orderStatusDesc = '已付款'
break
case 2: data[i].orderStatusDesc = '已取消'
break
case 3: data[i].orderStatusDesc = '退款中'
break
case 4: data[i].orderStatusDesc = '已退款'
break
case 5: data[i].orderStatusDesc = '退款失败'
break
}

if (moment().subtract(1, 'days').isAfter(data[i].effectiveDate.split('~')[1]) || moment().subtract(1, 'days').isAfter(data[i].effectiveDate)) {
data[i].orderStatusDesc = '已失效'
data[i].expire = true
}
}
if (isFirst) {
this.listOrders = data
this.$nextTick(() => {
if (data.length === 0) {


} else {
this.$refs.scroll.scroll.scrollTo(0, 0)
}
})
return
}

if (data.length === 0) {
this.$vux.toast.text('没有更多数据了', 'middle')
if (this.$refs.scroll && this.pageNum > 1) {
this.$refs.scroll.hiddenPullUpLoad()
} else {
this.$refs.scroll.hiddenPullDown()
}
} else {
if (this.pageNum > 1) {
this.listOrders = this.listOrders.concat(data)
} else {
this.listOrders = data
}
}
})
},

selectTabEvery (value) {
this.setOrderTab(value)
this.pageNum = 1
this.getWaitPayList(true)
},
handleToEvaluation (id, outTradeNo) {
sessionStorage.setItem('outTradeNo', outTradeNo)
this.$router.replace('/user/evalution/' + id)
},
toPay (outTradeNo) {
nativeBridge.alipay(outTradeNo, ({arg, msg}) => {

if (arg === 9000) {
this.$store.commit('SET_ORDER_TAB', 2)
this.getWaitPayList(true)
}
this.$vux.toast.text(msg, 'middle')
})
},
toCansel (outTradeNo) {
axios.put(this, '/v1/order', {outTradeNo: outTradeNo, orderStatus: 2}, (data) => {
this.$vux.toast.text('订单取消成功', 'middle')
this.getWaitPayList(true)
})
},
onPullingUp () {
this.pageNum++
this.getWaitPayList()
},
onPullingDown () {
this.pageNum = 1
this.getWaitPayList()
}
},
components: {
VHeader,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem,
Flexbox,
FlexboxItem,
Scroll,
Confirm,
CheckIcon
}
}



